import React ,{ useState , useEffect} from 'react'

const AuthorPhoto = () => {
    const [photo, setPhoto] = useState(null)
   
    const clickEv = (e) => {
        e.target.nextElementSibling.click()
    }
    const clickEv2 = (e) => {
        e.target.previousElementSibling.click()
    }
    const fileChange = (e) => {         
        try {
            let file = e.target.files[0];                     
            let img  = document.createElement('img');
            img.src = URL.createObjectURL(file);
            img.onload = () => {      
                 document.querySelector('.photo-container').append(img);                                                 
                 canvasFunc(img.offsetWidth,img.offsetHeight,img,setPhoto);
            }
        }catch(err) {
             console.log(err)
        }                  
   }
   useEffect(() => {
    let openRequest = indexedDB.open('article-store',2)
    openRequest.onupgradeneeded = () => {
        let db = openRequest.result;
        if (!db.objectStoreNames.contains('photoBuffer')) {
            db.createObjectStore('photoBuffer', {keyPath: 'name'});
          }            
    }     
    openRequest.onsuccess =async () => {
        let db = openRequest.result;
        let tx = db.transaction('photoBuffer')
        let store = tx.objectStore('photoBuffer')
        let photo = store.get('article-author-photo')
        photo.onsuccess = () => {            
            if(photo.result) {
                setPhoto(photo.result.file);
            }                                                    
        }
        
    } 
    return () => {
        URL.revokeObjectURL(photo);
    }
},[])
  

    return (
        <>
           
          { 
             !photo &&  <div className="file-upload-button" onClick={clickEv}>
                            
              </div>
           }
           <input type="file" name="author-photo-file" className='d-none' onChange={fileChange}/>
           {
               photo && <img src={URL.createObjectURL(photo)} alt="author-photo" onClick={clickEv2}/>
           }
           <div className="photo-container" style={{position:'fixed',
                    left:0,top:0,
                    visibility:'hidden',
                    userSelect:'none'                
            }}></div>
           <canvas id="canvas" hidden>
                     
           </canvas>
           <canvas id="canvas2" hidden >
            
           </canvas>

           <style jsx>{`
                  .file-upload-button {
                      width:80px;
                      height:80px;
                      background:black;
                      cursor:pointer;
                  }
                  img {
                      cursor:pointer;
                  }
               `}</style>
        </>
    )
}



const canvasFunc = (W,H,img,loadPhoto) => {
    let canvas = document.getElementById('canvas');
    let  ctx = canvas.getContext('2d');
    let count = Math.min(W,H);
    canvas.width = count;
    canvas.height = count;
    let w =(Math.max(W,H)-count)/2;    
    W>H?ctx.drawImage(img,-w,0): ctx.drawImage(img,0,-w); //裁减成一比一    
    let ctx2 = document.getElementById('canvas2').getContext('2d');
    canvas2.width = 80;
    canvas2.height= 80;
    ctx2.drawImage(canvas,0,0,80,80);  // 缩放成40px 40px

    document.getElementById('canvas2').toBlob((blob) => {  // 输出图片
       
       let name = 'authorPhoto.' + blob.type.slice(6,9);       
       let file = new File([blob],name);
       
       loadPhoto(blob)
       let openRequest = indexedDB.open('article-store',2)
       openRequest.onsuccess = () => {
           let DB = openRequest.result;                
           let transaction  =  DB.transaction('photoBuffer','readwrite');
           let store = transaction.objectStore("photoBuffer"); 
           let photo = store.get('article-author-photo')
           photo.onsuccess = () => {                         
                if(photo.result) {
                    store.delete('article-author-photo')                    
                }                                                    
                let bufferItem = {
                    name:'article-author-photo',
                    file,
                    create:new Date(),
                }
                let req = store.add(bufferItem);                
                req.onerror = () => {
                    console.log('存图片buffer 失败');                    
                    console.log(req.error);
                }           
            }            
       }   
    //    let a = document.createElement('a');       
    //    a.href = URL.createObjectURL(blob);  
    //    a.download = true;   
    //    a.click();     
},'image/png')

}







export default AuthorPhoto
