import React,{useState,useEffect} from 'react'

const Photo = ({ name, index, setState, title, state}) => {    
    

    const [File, setFile] = useState(null)

    const clickEv = (e) => {
       e.currentTarget.nextElementSibling.click()
    }
    useEffect(() => {
        let openRequest = indexedDB.open('article-store',2)
        openRequest.onupgradeneeded = () => {
            let db = openRequest.result;
            if (!db.objectStoreNames.contains('photoBuffer')) {
                db.createObjectStore('photoBuffer', {keyPath: 'name'});
              }            
        }     
        openRequest.onsuccess =async () => {
            let db = openRequest.result;
            let tx = db.transaction('photoBuffer','readwrite')
            let store = tx.objectStore('photoBuffer')
            let photo = store.get(name)            
            photo.onsuccess = () => {                
                if(photo.result) {                    
                    setFile(URL.createObjectURL(photo.result.file));
                }                                                                    
            }            
        } 

        return () => {
            URL.revokeObjectURL(File);
        }
    },[])

    const fileUp = (e) => {            
            let file = e.target.files[0];
            let src = URL.createObjectURL(file);
            setFile(src)
            let openRequest = indexedDB.open('article-store',2)
            openRequest.onsuccess = () => {
                let DB = openRequest.result;                
                let transaction  =  DB.transaction('photoBuffer','readwrite');
                let store = transaction.objectStore("photoBuffer"); // (2)
                let bufferItem = {
                    name,
                    file,
                    create:new Date(),
                }
                let req = store.add(bufferItem);
                req.onsuccess = () => {
                    console.log('存图片buffer 成功');
                    console.log(req.result);
                }
                req.onerror = () => {
                    console.log('存图片buffer 失败');                    
                    console.log(req.error);
                }           
            }               
    }

    const clearFile = (e) => {          //清楚file文件
        setFile(null);
        e.currentTarget.nextElementSibling.value = '';
    }

    const changValue = (e) => {      // 更改图片标题事件
        let oldValue =    localStorage.getItem('article-content-object') ;   
        if(!oldValue) return  
        let mianData = JSON.parse(oldValue)                  // 根据输入的值 更新状态和localstorage！！
        mianData[index].title = e.target.value;                    
        localStorage.setItem('article-content-object',JSON.stringify(mianData)) ;
        setState(mianData);
    }

    const deleteEv = (index) => {                 
          let newList = state.filter( (e,Cindex) => Cindex !== index)
          setState(newList);
          localStorage.setItem('article-content-object',JSON.stringify(newList));

          let openRequest = indexedDB.open('article-store',2)          
          openRequest.onsuccess = () => {
              let DB = openRequest.result;                
              let transaction  =  DB.transaction('photoBuffer','readwrite');
              let store = transaction.objectStore("photoBuffer"); // (2)              
              let req = store.delete(name);
              req.onsuccess = () => {
                  console.log(name + 'delete 成功');                  
              }
              req.onerror = () => {
                  console.log(name + 'delete 失败');
                  console.log(req.error);
              }           
          }               
    }
    

    return (
        <div className="row content-item position-relative" >
            <input type="text" name="photo-title"
                  onChange={changValue} 
                  onInput={changValue}
                  defaultValue={title? title : null} 
                  required
                   className='px-4 text-center'/>                    
            {
              File ? <img src={File} alt="content-photo" className='col-12 photo' onDoubleClick={clearFile}/>: <div onClick={clickEv} 
                                       className=' uploadRoot col-6 offset-3 d-flex justify-content-center align-items-center'> 
                                       <i className='iconfont icon-cloud-upload'></i>
                        </div>
            }            
            <input type="file" name={name + 'file'} id={name + 'file'} className='d-none' onChange={fileUp}/>
            <div className="delete" onClick={() => deleteEv(index)}>
                    <i className='iconfont icon-trash-'></i>
            </div> 
            <style jsx> {`
            .content-item {
                   margin-bottom:5rem;
               }    
               img {
                   width:100%;                   
               }
               .photo {
                    margin-bottom:3rem;
               }
               input[name='photo-title'] {
                   height:6vh;
                   width:50%;
                   margin:0 auto;
                   font-size:3rem;
                   line-height:2;
                   margin-bottom:5rem;
                  
               }
               .uploadRoot {
                   height:20vh;                   
               }
               i {
                   font-size:8rem;
                   height:8rem;
                   cursor:pointer;
                   transition:.2s;
               }
               i:hover {
                font-size:9rem;
                   height:9rem;
               }
               .delete {
                   position:absolute;
                   right:-50px;
                   top:50%;
                   
               } 
               .delete i {
                     background:none;
                     color:black;  
                     font-size:2rem;                   
               }
               .delete i:hover {
                    background:black;
                     color:white;                     
               }
                            `}
            </style>
        </div>
    )
}

export default Photo
